.footer {
  width: 100%; 
  padding: 60px 70px;
  background-color: var(--dark-purple);
  margin-top: 75px;
  left: 0;
  bottom: 0;
  justify-content: center;
  text-align: center;
}

.footer-container {
  text-align: center;
  justify-content: center;
  /* max-width: 1040px; */
  margin: auto;
  display: block;
  flex-wrap: wrap;
}

.footer-container a {
  color: var(--accent-color-lt-grey);
}

.contact {
  justify-content: center;
  text-align: center;
  display: block;
  margin-bottom: 30px;
}

.address {
  margin-bottom: 25px;
}

.footer p {
  color: var(--accent-color-lt-grey);
  font-weight: 300;
  font-size: 16px;
}

.contact-section {
  display: block;
}

.icons {
  padding-bottom: 10px;
  align-items: center;
}

.icons a {
  color: var(--accent-color-lt-grey);
  transition: all .3s;
}

.icons a:hover {
  opacity: 45%;
}

.icons p {
  font-weight: 300;
  font-size: 16px;
}

.home-container {
  justify-content: center;
  text-align: center;
  align-items: center;
  display: flex;
  color: var(--accent-color-lt-grey);
  font-weight: 300;
  font-size: 16px;
}

.home-container p {
  font-weight: 300;
}

.social-media h4 {
  color: var(--accent-color-lt-grey);
  letter-spacing: 1px;
  margin-bottom: 20px;
}

.social-icons {
  width: auto;
  text-align: center;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.social-icons a {
  color: var(--accent-color-lt-grey);
  transition: all .3s;
}

.social-icons a:hover {
  opacity: 45%;
}

.linkedin-icon p {
  font-weight: 400;
  font-size: 15px;
}

.github-icon {
  margin-left: 30px;
}

.github-icon p {
  font-weight: 400;
  font-size: 15px;
}

@media screen and (max-width: 576px) {
  .home-container {
    display: inline-block;
    margin-right: none;
    font-weight: 500;
  font-size: 13px;
  }

  .home-container p {
  font-size: 13px;
  font-weight: 500;
  }

  .icons a {
    font-size: 13px;
    font-weight: 500;
  }

  .social-media h4 {
    font-size: 14px;
    font-weight: 800;
  }

  .social-icons p {
    font-size: 13px;
    font-weight: 500;
  }
}

@media screen and (max-width: 390px) {
  .home-container {
  font-weight: 500;
  font-size: 13px;
}

  .linkedin-icon p {
  margin-bottom: 20px;
}

  .github-icon {
  margin-left: 0px;
}
}